<template>
  <!--博客详情页面 -->
  <!--  头部导航栏组件-->
  <div>
    <header
      :style="{backgroundImage: 'url(' + data.img + ')'}"
    >
      <Nav1 />
      <div class="back-img-content">
        <h1 v-if="data.title != null">{{ data.title }}</h1>
        <h1 v-else>努力加载中...</h1>
        <div class="back-img-content-desc" v-if="data.title != null">
          <Icon type="md-time" /> 发表于 {{ data.createTime }} |
          <Icon type="ios-time-outline" /> 更新于 {{ data.updateTime }} |
          <Icon type="ios-filing-outline" />
          <a :href="data.fileUrl" target="_blank" v-if="data.fileName != ''">{{
            data.fileName
          }}</a>
          <a v-else>——</a>
          <br />
          <Icon type="md-time" /> 阅读时长：{{ data.readTime }} 分钟 |
          <Icon type="ios-eye" /> 阅读量：{{ data.readNum }}
        </div>
      </div>
    </header>
    <!-- 主体部分组件 -->
    <main id="main">
      <!-- 中间博客内容 -->
      <div class="main-1" id="main-1">
        <PageDesc :mdurl="data.MDUrl" />
      </div>
      <!-- 右侧个人简介 -->
      <div id="my" class="my">
        <Profile />
      </div>
    </main>
    <!-- 底部 -->
    <Footer />
  </div>
</template>
<script>
import Nav1 from "../nav/Nav";
import PageDesc from "../pageDesc/PageDesc.vue";
import Profile from "../profile/Profile";
import Footer from "../footer/Footer";
export default {
  data() {
    return {
      data: new Array(), //根据id获取指定数据
    };
  },
  methods: {
    //根据路由传递的id进行查询指定数据
    getData() {
      this.data = new Array();
      window.scrollTo(0, 0); //页面到顶部
      let _this = this;
      let _id = this.$route.query.id; //参数会显示在url上
      let _length = this.$store.getters.getAllBlog.length;
      let _data = this.$store.getters.getAllBlog;
      for (var i = 0; i < _length; i++) {
        if (_data[i].id == _id) {
          _this.data = _data[i];
        }
      }
    },
  },
  mounted() {
    this.getData();
  },
  components: {
    Nav1,
    Profile,
    PageDesc,
    Footer,
  },
};
</script>
<style scoped>
@import "../main/Main";
@import "./Desc";
</style>


