<template>
  <!-- 详细博客内容 -->
  <div class="pageDesc mavonEditor">
    <mavon-editor
      :value="handbook"
      :boxShadow="false"
      :ishljs="true"
      :subfield="prop.subfield"
      :defaultOpen="prop.defaultOpen"
      :toolbarsFlag="prop.toolbarsFlag"
      :editable="prop.editable"
      :scrollStyle="prop.scrollStyle"
    />
    <div class="desc-title">
      <Icon type="ios-text" /> <strong>评论区</strong>
    </div>
    <!-- 引入valine评论区 -->
    <div id="vcomments"></div>
  </div>
</template>
<script>
export default {
  name: "PageDesc",
  props: ["mdurl"],
  watch: {
    $route(to, from) {
      if (to.path !== from.path) {
        setTimeout(() => {
          //重新刷新valine
          this.createValine();
        }, 300);
      }
    },
    mdurl: function (newVal, oldVal) {
      this.getData(newVal);
    },
  },
  data() {
    return {
      markdownOption: {
        bold: true, // 粗体
      },
      handbook: "",
      Valine: "",
    };
  },
  computed: {
    data() {
      return this.$page.frontmatter;
    },
    prop() {
      let data = {
        subfield: false, // 单双栏模式
        defaultOpen: "preview", //edit： 默认展示编辑区域 ， preview： 默认展示预览区域
        editable: false, // 是否允许编辑
        toolbarsFlag: false,
        scrollStyle: true,
      };
      return data;
    },
  },
  methods: {
    getData(data) {
      this.$axios({
        url: process.env.API_MD + data + ".md",
      })
        .then((res) => {
          this.handbook = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    createValine() {
      const Valine = require("valine");
      window.AV = require("leancloud-storage");
      const valine = new Valine({
        el: "#vcomments",
        appId: "HVACcWvDmLuvIaYCSC98Najw-gzGzoHsz",
        appKey: "O48jmTv9FxHE5veibgRppj05",
        notify: false,
        verify: false,
        avatar: "monsterid",
        pageSize: "6",
        path: window.location.href,
        placeholder: "留下你想说的话吧^_^",
      });
      this.valineRefresh = false;
    },
  },
  mounted() {
    this.createValine();
  },
};
</script>
<style scoped>
.pageDesc {
  width: 100%;
  text-align: left;
  border-radius: 10px;
  box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.05);
  -webkit-box-shadow: #d4d2d2 0px 0px 10px;
  -moz-box-shadow: #d4d2d2 0px 0px 10px;
  padding: 1rem;
}
.pageDesc:hover {
  box-shadow: 5px 5px 5px 0 rgba(223, 212, 212, 0.1);
  -webkit-box-shadow: rgb(177, 173, 173) 0px 0px 10px;
  -moz-box-shadow: rgb(177, 173, 173) 0px 0px 10px;
}
#vcomments {
  width: 100%;
  margin-top: 0.5rem;
}
.desc-title {
  margin-top: 2rem;
  font-size: 1.5rem;
}
</style>


